.jsplayground ul {
  list-style: none;
  margin: 0;
  padding: 0 0 0 51px; }
  .jsplayground ul > li {
    position: relative;
    margin-bottom: 25px; }
    .jsplayground ul > li > i {
      position: absolute;
      left: -50px;
      top: -3px; }

.not-active {
  color: #c4c4c4;
  pointer-events: none;
  cursor: default; }



